@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@400;500;600;700&display=swap);
*{
  margin:0;
  padding:0;
  box-sizing: border-box;
}
html{
  font-size: 16px;
}
.navbar{
width: 100%;
height: 4.5rem;
background: #FFFFFF;
box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.12);
display: flex;
justify-content: space-between;
}
.logo p{
  line-height: 4.5rem;
  margin-left: 3rem;
  color:#5C5D5D;
  font-family: sans-serif;
  font-size: 1.5rem;
}
.logo span {
  color:#27A365
}
.logo2 {
  background: linear-gradient(138.44deg, #00A854 23.11%, #004D25 76.5%);
}

.search-bar{
  height: 2.6rem;
  width: 22.5rem;
  border-radius: 21px;
  background: #F2F2F2;
  margin-top: 1rem;
  display: flex;
}
.search-bar i {
  position: relative;
  top: 0.8rem;
  left:1rem;
  color:#7A7A7A;
}
.search-bar p {
font-family: IBM Plex Sans;
font-size: 1rem;
font-style: normal;
font-weight: 500;
margin: auto 2rem;
}

.account{
  font-family: IBM Plex Sans;
  font-size: 1rem;
  font-weight:500;
  font-style: normal;
  line-height:4.5rem;
  margin-right: 2rem;
}
.main {
  height: 27.5rem;
  width: 100%;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.45) 0%, rgba(0, 0, 0, 0.6) 100%), url(/static/media/bg.5452e362.jpg);
  background-size: cover;
  background-position: center center;
}
.top {
  display: flex;
  justify-content: space-between;
}
.group {
  font-family: IBM Plex Sans;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 1rem;
  color: white;
  padding:8px 6px 8px 6px;
  border:2px solid white;
  margin:1rem;
}
.fa-arrow-left {
  color: white;
  margin: 20px 0 0 20px;
}
.main h1 {
  font-family: IBM Plex Sans;
  font-size: 2.25rem;
  font-style: normal;
  font-weight: 700;
  line-height: 4rem;
  color:#fff;
  position: absolute;
  left: 12.5rem;
  top: 22.37rem;

}

.main p{
  font-family: IBM Plex Sans;
  font-size: 1rem;
  font-style: normal;
  font-weight: 400;
  line-height: 1.5rem;
  /* height: 23px;
  width: 327px;*/
  left: 12.5rem; 
  top: 25.56rem;
  color:white;
  position: absolute;
}
.body, .mobile-view{
  /* background-color:#f2f2f2; */
  height: auto;
  width: 73%;
  margin:0 auto;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  padding-top: 2rem;

}
.container {
  flex: 0.4 1;
}
.right{
  flex:0.3 1;
  display: flex;
  justify-content: center;
}
.groups {
  background-color: #000;
  flex:0.4 1;
}
.rectangle{
  background-color:#7A7A7A;
  flex:0.4 1;
}
.container {
  /* position: absolute; */
  /* width: 22.375rem;*/
  height: 1.3125rem; 
  left: 12.5rem;
  top: 35rem;
}
.container ul {
  display: flex;
  justify-content: center;
  list-style: none;
}
.container li {
  font-family: IBM Plex Sans;
  font-size: 1rem;
  font-style: normal;
  font-weight: 400;
  line-height: 1.3rem;
  color: #8A8A8A;
  padding-bottom: 1.3rem;
  margin-right: 1rem;
}
.container li.active {
  font-weight: 500;
  color:Black;
  border-bottom: 1.5px solid #000000;
} 
hr {
  width: 73%;
  margin:0 auto;
  margin-top: 1rem;
}
.rectangle, .groups {
  border-radius: 4px;
  background: #EDEEF0;
  margin-top: -0.5rem;
  margin-right: 0.8rem;
  line-height:2rem;
  font-family: IBM Plex Sans;
  font-size: 1rem;
  font-style: normal;
  font-weight: 500;
  display: flex;
  justify-content: center;
  width: 14rem;
  height: 2.25rem;
  height:auto;
}
.rectangle p, .groups p {
font-family: IBM Plex Sans;
font-size: 1rem;
font-style: normal;
font-weight: 500;
margin: auto 0;
}

.rectangle i {
  line-height: 2.25rem;
  margin-left: 1rem;
  position: relative;
  right:0.5rem;
}
.groups {
  background-color: #2F6CE5;
  color:#ffffff;
  margin-right: 0rem;
  width: 8rem;
  /* width: auto;*/
  height: auto; 

}
.groups i {
  line-height: 2.25rem;
  margin-right: 0.5rem;
}

.posts{
  height: auto;
  margin: 3rem auto;
}

.post{
  width: 95%;
  height: 29.5rem ;
  height: auto;
  border-radius: 4px;
  border: 1px solid #E0E0E0;
  position: relative;
}

.image img{
  width: 100%;
  height: 14rem;
  border-radius: 4px 4px 0px 0px;
}
.text{
  font-family: IBM Plex Sans;
}
.text p{

line-height: 23px;
font-style: normal;
font-weight: 500;
font-size: 18px;
font-weight: 400;
padding: 1.25rem;
padding-bottom: 10px;
padding-left: 10px;
}
.text h1 {
font-size: 1.6rem;
font-style: normal;
font-weight: 600;
line-height: 1.95rem;
padding: 0 0 12px 1.25rem;
}
.text h3 {
font-size: 1.2rem;
font-style: normal;
font-weight: 400;
line-height: 25px;
padding:0 0 1.75rem 1.25rem;
color: #5C5C5C
}
.lower, .lower2{
  display: flex;
  justify-content:space-between;
  padding-bottom: 1.25rem;
}
.name {
   display: flex;
   /* flex-direction: column; */
   padding-bottom: 20px;
}
.profile  {
  width: 48px;
  height: 48px;
  background: url(/static/media/bg.5452e362.jpg);
  border-radius: 1.5rem;
  margin-left : 20px;
  margin-bottom: 20px;
}
.name p {
  font-family: IBM Plex Sans;
  font-style: normal;
  font-weight: 600;
  font-size: 1.1rem;
  line-height: 0rem;
color: #000000;
}
.fa-share-alt{
  color:#2D2D2D;
  padding: 10.5px;
  background-color: #EDEEF0;
  border-radius: 2px;
  margin-right: 1.625rem;
  margin-bottom: 1.625rem;
}
.far {
  color:#DADADA;
  margin-right: 0.8rem;
}
.views span {
  font-family: IBM Plex Sans;
  font-size: 1rem;
  font-style: normal;
  font-weight: 500;
  line-height: 1.1rem;
  margin-right: 2.5rem;
  color:#525252;

}
.body2{
  width:73%;
  margin: 0 auto;
  display: flex;
  justify-content:space-between;
}
.left {
  width:70%;
}
.rightpart{
  width:30%;
  margin-top: 3rem;
  padding-left: 3rem;
}
.rightline {
  width: 100%;
  margin-bottom: 2rem;
}
.righter span {
  font-family: IBM Plex Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.1rem;
  margin-left: 11.2px;
  margin-right: 7.906rem;
}
.fa-exclamation-circle span {
  font-family: IBM Plex Sans;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 1rem;
  opacity: 0.5;
  position: absolute;
  width: 15%;
  /* height: 2rem; */

}
.fa-exclamation-circle {
  opacity: 0.5;
}
.mobile {
  flex-direction: row;
}
.mobile span {
  margin-left: 0.8rem;
}
@media (max-width:900px) {
  .body2{
    display: flex;
    flex-direction: column;
  }
  .left {
    width:100%;
  }
  .rightpart{
    width:100%;
  }
  .fa-exclamation-circle span {
    width: 50%;
  }
}
@media(min-width: 900px){
  .top{
    display: none;
  }
  .mobile-view {
    display: none;
  }
  

}
@media(max-width: 900px){
  .search-bar {
    display:none;
  }
  .account {
    display: none;
  }
  .rectangle{
    width: auto;
  }
  .main h1 {
    left: 1.5rem;
    top: 18.37rem;
    font-size: 1.5rem;
  }
  
  .main p{
    left: 1.5rem; 
    top: 21rem;
  }
  .body {
    display:none;
  }
  .body2 {
    width: 95%;
  }
  .post {
    width: 100%;
  }
  .rightpart{
    display:none;
  }
  hr {
    display: none;
  }
  .container{
    justify-content: flex-start;
  }
  
}

@media(min-width:567px){
    .lower2{
        display: none;
    }
}

@media(max-width:567px){
  .lower{
      display: none;
  }
}
@media(max-width:1330px){
  .righter span {
    margin-right:5rem;;
  }
  .rightpart {
    padding-left: 0;
  }
}


